.cover-photo-container {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative; }
  .cover-photo-container.big {
    height: 100vh;
    top: -80px;
    margin-bottom: -80px; }
  .cover-photo-container.small {
    height: 50vh;
    top: -45px;
    margin-bottom: -45px; }
  .cover-photo-container .photo {
    height: 100%;
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center; }
  .cover-photo-container .overlay {
    background-color: rgba(0, 0, 0, 0.5);
    height: 100%;
    width: 100%;
    position: absolute; }
  .cover-photo-container .content {
    display: flex;
    flex-direction: column;
    position: absolute;
    margin-top: -60px; }
    .cover-photo-container .content img {
      width: 170px;
      margin: 10px auto; }
    .cover-photo-container .content .title {
      font-size: 60px;
